.App {
  width: 360px;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  background: linear-gradient(126.51deg, #E8F1FF 28.73%, rgba(232, 241, 255, 0) 133.6%);
}

h1 {
  font-family: 'Lobster', cursive;
  font-size: 57px;
  text-align: center;
}

.Players {
  flex-grow: 1;
  width:300px;
  margin: 0 auto;
}

